import { memo, useEffect } from "react"
import { useMemo, useState, useCallback } from "react"

const Child = ({name = {},text, count, obj}) => {
  console.log('%c name =========>', 'color:#722ed1;', text)
  let memoObj = useMemo(() => {
    console.log('%c useMemoObj =========>', 'color:#722ed1;', )
    return obj
  }, [obj]) // useMome
  return (
    <div>
      {name.count}
      <br/>
      {name.text}
      <p>{count}</p>
      {/*  str numberusememousemome */}
    </div>
  )
}
const CallBackChild = ({name, onchange}) => {
  console.log('%c render =========>', 'color:#722ed1;', name)
  return (
    <div>
      {name}
      <br/>
      <button onClick={() => {onchange(1)}}>add</button>
    </div>
  )
}

const MemoChild = memo(Child)
const MemoCallBackChild = memo(CallBackChild)
const TestUseMome = () => {
  const [text, setText] = useState('')
  const [count, setcount] = useState(0)
  const [count2, setCount2] = useState(0)
  const [obj, setObj] = useState({a: 12})
  const name = useMemo(() => ({
    text,
    count,
  }), [text]) //  vuecomputed
  return (
    <div>
      hook
      {text}
      <input type="text" value={text} onChange={(el) => {
        setText(el.target.value)
      }} />
      <button onClick={() => {setcount(state => state + 1)}}>add</button>
      <button onClick={() => {setCount2(state => state + 1)}}>{count2} addCount2</button>
      <button onClick={() => {setObj({a: 13})}}>change Obj</button>
      <p>Child  name = usememo</p>
      <Child name={{text, count,}} text="normal"></Child>
      <p>Child  name = usememo; 
        <p>text</p>
        <p>render</p>
      </p>

      <Child name={name} text="Child + useMome"></Child>
      <p>memoChild  name = useMome
        <p>mome comp  +  name usememo  namerender</p>
        {/* count mome + count2  count2string  momerendermemoprops  */}
      </p>
      <MemoChild name={name} text="MemoChild + useMome" count={count2}></MemoChild>
      <p>
        memochild + state  render
      </p>
      <MemoChild text="MemoChild + not useMome + nochange obj" obj={obj} count={count2}></MemoChild>
      <p> childusememo </p>
      <Child text="Child + nochange obj + useMemo" obj={obj} count={count2}></Child>
    </div>
  )
}

const TestUseCallBack = () => {
  const [text, setText] = useState('')
  const [count2, setCount2] = useState(0)
  let onchange = (val) => {
    console.log('%c val =========>', 'color:#722ed1;', val)
  }
  let useCallOnchange = useCallback((val) => {
    console.log('%c val =========>', 'color:#722ed1;', val)
  }, [count2])
  useEffect(() => {
    console.log('%c useCallOnchanging =========>', 'color:#722ed1;', )
  }, [useCallOnchange])
  return (
    <div>
      <p style={{width: '100%', borderTop: '1px solid red'}}></p>
      {text}
      <input type="text" value={text} onChange={(el) => {
        setText(el.target.value)
      }} />
      <button onClick={() => {setCount2(state => state + 1)}}>{count2} addCount2</button>
      <p>useCallback Count2</p>
      <p>Child onchange = onchange:</p>
      <CallBackChild name="normal" onchange={onchange}></CallBackChild>
      <p>callBackChild onchange = onchange:</p>
      <CallBackChild name="child + callback" onchange={useCallOnchange}></CallBackChild>
      <p>MemoCallBackChild onchange = onchange:</p>
      <MemoCallBackChild name="Memochild + CallBack" onchange={useCallOnchange}></MemoCallBackChild>
    </div>
  )
}
const View = () => {
  return(
    <div>
      <TestUseMome></TestUseMome>
      <TestUseCallBack/>
    </div>
  )
}
export default View