import React, {useCallback,useState, useRef, useLayoutEffect, useEffect} from 'react'

function CallBackTest() {
  const [text, updateText] = useState('');

  const textRef = useRef();

  useLayoutEffect(() => {
    textRef.current = text; // useLayoutEffect didUpdate text  ref
  });

  const handleSubmit = useCallback(() => {
    console.log('%c handleSubmit =========>', 'color:#722ed1;', text)
    console.log('%c textRef =========>', 'color:#722ed1;', textRef.current)
  }, []); // texttext, text,handleSubmit,usecallback
  const handleClick = () => {
    console.log('%c handleClick =========>', 'color:#722ed1;', )
  }
  return (
    <>
      <input value={text} onChange={(e) => updateText(e.target.value)} />
      <button onClick={handleSubmit}>handleSubmit</button>
      <button onClick={handleClick}>handleClick</button>
    </>
  );
}

function Sixunhuan() {
  let count = 0;

  function App() {
    const [val, setVal] = useState("");

    function getData() {
      setTimeout(() => {
        setVal("new data " + count);
        count++;
      }, 500);
    }

    return <Child val={val} getData={getData} />;
  }

  function Child({val, getData}) {
    useEffect(() => {
      getData();
    }, [getData]);

    return <div>{val}</div>;
  }
  return <App/>
}

function MemoAndUsecallback() {
  const Child = React.memo(function({name, val, onChange}) {
    console.log('render...', name);
    
    return <input value={val} onChange={onChange} />;
  });
  
  function App() {
    const [val1, setVal1] = useState('');
    const [val2, setVal2] = useState('');
  
    const onChange1 = useCallback( evt => {
      setVal1(evt.target.value);
    }, []);
  
    const onChange2 = useCallback( evt => {
      setVal2(evt.target.value);
    }, []);
    // const onChange1 = evt => {
    //   setVal1(evt.target.value);
    // }
  
    // const onChange2 = evt => {
    //   setVal2(evt.target.value);
    // }
  
    return (
    <>
      <Child name="1" val={val1} onChange={onChange1}/>
      <Child name="2" val={val2} onChange={onChange2}/>
    </>
    );
  }
  return <App/>
}

